import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	components: {uniLoadMore},
	data() {
		return{
			status:"loading",
			topObj:{},
			pageNo:1,
			pageAll:2,
			userList:[]
		}
	},
	methods:{
		search(){
			let that=this;
			if(that.pageNo<=that.pageAll){
				that.$ajax({
					url:"/memberManager/searchUserDirectUserNumForPage",
					data:{
						pageNo:that.pageNo,
						pageSize:20,
						searchData: {
						  merchantId: that.shopObj.merchantId
						},
					},
					success(d){
						that.pageNo=that.pageNo*1+1;
						that.pageAll=d.totalPage
						console.log(d);
						if(that.pageNo==2){
							that.userList=d.lists
						}else{
							let arr=that.userList;
							that.userList=that.userList.concat(d.lists)
						}
						console.log(that.userList)
						if(that.pageNo>that.pageAll){
							that.status="noMore"
						}
					}
				})
			}
		}
	},
	onReachBottom(){
		this.search()
	},
	onShow(){
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
		that.$ajax({
			url:"/memberManager/searchUserDirectUserNumTop",
			data:{
				merchantId:that.shopObj.merchantId
			},
			success(d){
				that.topObj=d;
				that.search()
			}
		})
	}
}
